<template>
  <div class="recommend">
    <div class="title">
      <div class="title-name">每日开眼精选</div>
      <div class="other" @click="$router.push('/home/daily')">查看往期</div>
    </div>
    <VideoItemCard v-for="(item, index) in RecommendVideosList" :item="item" :key="index" @VideoItemCardClick="VideoItemCardClick($event)"></VideoItemCard>
  </div>
</template>

<script>
import VideoItemCard from "@/components/VideoItemCard.vue";
export default {
  name: "recommend",
  data() {
    return {
      RecommendData: {}
    };
  },
  methods: {
    VideoItemCardClick($event){
      this.$router.push('/playingdetailpage');
      this.$store.commit('setPlayingPageDate',$event)
      this.$store.commit('setShowBottomBar',false)
    }
  },
  computed: {
    RecommendVideos() {
      if (this.RecommendData.issueList) {
        return this.RecommendData.issueList[0];
      } else {
        return [];
      }
    },
    RecommendVideosList() {
      let arr = [];
      for (let key in this.RecommendVideos.itemList) {
        if (this.RecommendVideos.itemList[key].type == "video") {
          arr.push(this.RecommendVideos.itemList[key]);
        }
      }
      return arr;
    }
  },

  created() {
    this.RecommendData = this.$store.state.RecommendedToday;
    this.axios.get(`https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v2/feed`)
    .then(res => {
this.RecommendData = res.data;
        this.$store.commit(`setRecommendedToday`,res.data)
    })
    .catch(err => {
      if(err){
        this.axios
      .get(`/api/v2/feed`)
      .then(res => {
        this.RecommendData = res.data;
        this.$store.commit(`setRecommendedToday`,res.data)
      });

      }
      
    })
    
  },
  components: {
    VideoItemCard
  }
};
</script>

<style lang="less" scoped>
.recommend {
  .title {
    display: flex;
    .title-name {
      flex: 1;
      font-weight: bold;
      font-size: 18px;
    }
    .other{
    color: #1a73e8;
    font-size:14px;

    }
  }
}
</style>